.feedback {
  background-color: $additional-bg_color;
  border-radius: 0 0 30px 30px;
  box-shadow: 0 30px 0 $basic-yellow;

  @media (min-width: $tablet-width) {
    border-radius: 0 0 60px 60px;
    box-shadow: 0 60px 0 $basic-yellow;
  }
}

.feedback__container {
  padding-top: 70px;
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    padding-top: 72px;
    padding-bottom: 59px;
    padding-right: 69px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 105px;
    padding-bottom: 102px;
    padding-left: 132px;
    padding-right: 134px;
  }
}

.feedback__header {
  margin: 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    padding-bottom: 59px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 57px;
  }
}

.feedback__text {
  margin: 0;
  padding-bottom: 30px;
  font-family: $main-font;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: $basic-white;

  @media (min-width: $tablet-width) {
    padding-bottom: 69px;
    font-size: 28px;
    line-height: 32px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 62px;
  }
}

.feedback__span {
  @media (min-width: $tablet-width) {
    padding-left: 11px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 20px;
  }
}

.feedback__link {
  color: $basic-yellow;

  &:hover {
    color: $special-orange;
  }

  &:active {
    color: rgba($special-orange, 0.3);
  }
}
