.index-link {
  display: none;

  @media (min-width: $tablet-width) {
    position: relative;
    display: block;
    margin-left: auto;
    padding-right: 60px;
    font-family: $main-font;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    color: $basic-white;
    text-decoration: none;

    &:hover {
      color: rgba($basic-white, 0.6);
    }

    &:active {
      color: rgba($basic-white, 0.3);
    }
  }
}
