.header-title {
  padding-top: 45px;
  padding-left: 25px;
  padding-bottom: 15px;
  padding-right: 25px;

  @media (min-width: $tablet-width) {
    padding-top: 130px;
    padding-bottom: 70px;
    padding-left: 75px;
    padding-right: 75px;

    &--extended {
      display: flex;
      align-items: center;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 125px;
    padding-right: 123px;
    padding-top: 116px;
    padding-bottom: 71px;
  }
}
